// Options
$enable-rem: false;


// Colors

// Spacing
$spacer: px(16);
$spacers: (
  0: 0,
  1: ($spacer * .25),
  2: ($spacer * .5),
  3: $spacer,
  4: ($spacer * 1.5),
  5: ($spacer * 3)
);


// Body
$body-bg: #ffffff;
$body-color: #000000;


// Links
$link-color: #828282;


// Grid
$grid-gutter: 20px;
$grid-columns: 12;
$breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px
);


// Fonts
$font-family: OpenSans, Arial, sans-serif;
$font-size: px(16);
$line-height: 1.4;

$font-secondary: PlayfairDisplay, Arial, sans-serif;


// Layout
$site-padding: 20px;
$site-padding-mobile: 16px;
$min-width: 320px;
$max-width: 1440px;


// Transitions
$curves: (
  default:  cubic-bezier(0.4, 0, 0.2, 1), // fast-out-slow-in
  enter:    cubic-bezier(0, 0, 0.2, 1), // linear-out-slow-in
  leave:    cubic-bezier(0.4, 0, 1, 1), // fast-out-linear-in
  stand:    cubic-bezier(0.25, 0.8, 0.25, 1), // fast-in-fast-out
  drop:     cubic-bezier(0.55, 0, 0.55, 0.2)
);
$transition: 0.3s curve(default);


// Components
$header-height-mobile: 59px;



