@import "../../styles/functions";
@import "../../styles/variables";
@import "../../styles/mixins";

.footer {

  $this: &;

  &__container {
    position: relative;
    padding-top: 34px;
    padding-bottom: 34px;
    display: flex;

    @include below(lg) {
      display: block;
      padding-top: 39px;
    }

    &:before {
      content: '';
      background-color: #e1e1e1;
      position: absolute;
      height: 1px;
      top: 0;
      left: $site-padding;
      right: $site-padding;

      @include below(lg) {
        left: $site-padding-mobile;
        right: $site-padding-mobile;
      }
    }
  }

  &__item {

    & + & {

      @include below(lg) {
        margin-top: 24px;
      }
    }

    &_one {
      //width: 21.3%;

      @include above(lg) {
        max-width: 279.02px;
        padding-right: 94px;
      }
    }
    &_two {
      //width: 28.4%;

      @include above(lg) {
        max-width: 372.03px;
        width: 100%;
      }
    }
    &_three {

    }
    &_four {
      margin-left: auto;

      @include below(lg) {
        display: none;
      }
    }
  }

  &__title {
    margin-bottom: 29px;

    @include below(lg) {
      letter-spacing: 1.6px;
      text-transform: uppercase;
      margin-bottom: 16px;
    }
  }

  &__list {

    &_row {
      display: flex;
      flex-wrap: wrap;
    }

    &_single {

      & + & {
        margin-left: 22px;

        @include below(lg) {
          margin-left: 28px;
        }
      }
    }
  }

  &__list-item {
    margin-bottom: 13px;

    @include below(lg) {
      margin-bottom: 10px;
    }

    #{$this}__list_row & {
      width: percentage(1/2);

      @include below(lg) {
        width: percentage(1/3);
      }
    }
  }

  &__lists {
    display: flex;
  }
}



