@import "../../styles/functions";
@import "../../styles/variables";
@import "../../styles/mixins";

.one-check {
  display: inline-flex;
  align-items: center;
  cursor: pointer;

  $this: &;

  &__input {
    display: none;

    &:checked + #{$this}__box {
      border-color: #000000;
      background-color: #000000;

      #{$this}__box-icon {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__box {
    width: 20px;
    height: 20px;
    border-radius: 3px;
    border: solid 1px #acb4be;
    position: relative;
    transition: all $transition;
  }

  &__box-icon {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    font-size: 11px;
    transition: all $transition;
    opacity: 0;
    visibility: hidden;
  }

  &__legend {
    margin-left: 10px;
  }
}



