@import "../../styles/functions";
@import "../../styles/variables";
@import "../../styles/mixins";

.checkout {
  background: #F6F6F6;
  padding: 33px;

  &__heading {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -$grid-gutter/2;
    margin-right: -$grid-gutter/2;
  }

  &__col {
    width: 100%;
    padding: 0 $grid-gutter/2;

    &_half {
      width: percentage(1/2);
    }
  }


  &__drop {

  }

  &__drop-control {
    display: inline-flex;
    align-items: center;
    cursor: pointer;
  }

  &__drop-control-icon {
    width: 21px;
    height: 21px;
    border: solid 1px #828282;
    border-radius: 50%;
    flex: none;
    position: relative;

    &:before,
    &:after {
      content: '';
      background-color: #828282;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }

    &:before {
      width: 11px;
      height: 1px;
    }

    &:after {
      height: 11px;
      width: 1px;
    }
  }

  &__drop-control-legend {
    color: #828282;
    margin-left: 3px;
  }

}



