*, *:before, *:after {
  box-sizing: border-box;
}

:focus {
  outline: none;
}

::selection {
  background: #b3d4fc;
  text-shadow: none;
}

html {
  font-size: 16px;
}

body {
  font-size: $font-size;
  font-family: $font-family;
  line-height: $line-height;
  min-width: $min-width;
  color: $body-color;
  background-color: $body-bg;
}

audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}

img {
  max-width: 100%;
  height: auto;
}

button,
input,
optgroup,
select,
textarea {
  font: inherit;
}

button {
  padding: 0;
  background: none;
  border: none;
}

textarea {
  resize: vertical;
}

ol,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

td,
th {
  padding: 0;
}

a {
  text-decoration: none;
  color: $link-color;
  transition: $transition;

  &:hover {
    color: #000;
  }
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
  margin-top: $spacer;
  margin-bottom: $spacer;
  border: 0;
  border-top: 1px solid rgba(#000000, .1);
}

p {
  margin-top: 0;
  margin-bottom: $font-size;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: ($spacer / 2);
}

h1 {
  font-family: $font-secondary;
  font-size: 50px;
  font-weight: normal;
  line-height: 1.1;
  margin-bottom: 40px;

  @include below(lg) {
    font-size: 40px;
  }

  @include below(md) {
    font-size: 34px;
  }
}



