@import "../../styles/functions";
@import "../../styles/variables";
@import "../../styles/mixins";

.grid {
  display: flex;

  @include below(lg) {
    flex-direction: column;
  }

  $this: &;

  &__content {
    flex: auto;
  }

  &__side {
    flex: none;

    #{$this}_sm & {

      @include above(lg) {
        width: 472px;
      }
    }

    #{$this}_md & {

      @include above(lg) {
        width: 357px;
      }
    }

    #{$this}_contacts & {

      @include above(lg) {
        width: 546px;
      }
    }

    @include below(lg) {
      width: auto;
      margin-top: 39px;
    }
  }
}



