@function strip-unit($number) {
  @if type-of($number) == "number" and not unitless($number) {
    @return $number / ($number * 0 + 1);
  }

  @return $number;
}


@function rem($px, $root: 16) {
  $px: strip-unit($px);
  $root: strip-unit($root);
  @return $px / $root * 1rem;
}


@function em($px, $context) {
  $px: strip-unit($px);
  $context: strip-unit($context);
  @return $px / $context * 1em;
}


@function vw($px, $root: $max-width) {
  $px: strip-unit($px);
  $root: strip-unit($root);
  @return $px / $root * 100 * 1vw;
}


@function vh($px, $root: 850) {
  $px: strip-unit($px);
  $root: strip-unit($root);
  @return $px / $root * 100 * 1vh;
}


@function px($px) {
  $px: strip-unit($px);
  @return if($enable-rem, rem($px), $px * 1px);
}


@function _px($px) {
  $px: strip-unit($px);
  @return $px * 1px;
}


@function str-replace($string, $search, $replace: "") {
  $index: str-index($string, $search);

  @if $index {
    @return str-slice($string, 1, $index - 1) + $replace + str-replace(str-slice($string, $index + str-length($search)), $search, $replace);
  }

  @return $string;
}


@function linear($value-start, $value-end, $width-start: $width-start, $width-end: $width-end) {
  $tangent: ($value-end - $value-start) / ($width-end - $width-start);
  @return calc(#{$value-start} + (100vw - #{$width-start}) * #{$tangent});
}


@function z($key, $map: $z-indexes) {
  @return map-get($map, $key);
}


@function bp($key, $map: $breakpoints) {
  @return map-get($map, $key);
}


@function spacer($key, $map: $spacers) {
  @return map-get($map, $key);
}


@function curve($key, $map: $curves) {
  @return map-get($map, $key);
}


@function color($color, $palette: $colors) {
  // Parse arguments
  $color: map-get($palette, $color) or $color;
  $base: nth($color, 1);
  $adjust: if(length($color) > 1, nth($color, 2), ());
  // Recursive check
  $color: if(map-has-key($palette, $base), color($base, $palette), $base);
  // Adjustments
  @each $function, $value in $adjust {
    $color: call($function, $color, $value...);
  }
  @return $color;
}



