@import "../../styles/functions";
@import "../../styles/variables";
@import "../../styles/mixins";

.changes {
  text-align: right;
  position: relative;

  & + & {
    margin-top: 8px;
  }

  &:after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    background-color: #ffffff;
    width: 15px;
    right: 0;
    pointer-events: none;
  }

  &:before {
    content: '';
    width: 15px;
    height: 9px;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    right: 0;
    pointer-events: none;
    z-index: 1;
    background: url("../../img/arrow-down.svg") no-repeat center/cover;
  }

  &__select {
    cursor: pointer;
    border: none;
    padding-right: 5px;
    background: #ffffff;
    color: $link-color;
  }
}



