@import "../../styles/functions";
@import "../../styles/variables";
@import "../../styles/mixins";

.post {

  &__box {

    & + & {
      margin-top: 50px;
    }

    p {
      margin-bottom: 10px;
    }
  }

  &__subtitle {
    font-size: 20px;
    line-height: 1.41;
    letter-spacing: 4px;
    text-transform: uppercase;
    margin-bottom: 5px;

    @include below(lg) {
      font-size: 16px;
      margin-bottom: 11px;
    }
  }
}



