@import "../../styles/functions";
@import "../../styles/variables";
@import "../../styles/mixins";

.social {
  display: flex;
  align-items: center;

  &__item {

    & + & {
      margin-left: 20px;
    }
  }

  &__link {
    width: 48px;
    height: 48px;
    background-color: #f8f8f8;
    border-radius: 50%;
    display: block;
    position: relative;
  }

  &__icon {
    font-size: 22px;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;

    &.icon-fb {
      font-size: 24px;
    }

    &.icon-vk {
      font-size: 15px;
    }
  }
}



