@import "../../styles/functions";
@import "../../styles/variables";
@import "../../styles/mixins";

.logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  &__link {
    color: #000000;
    transition: none;

    &:hover {
      color: #ff9999;
    }
  }

  &__img {
    max-height: 60px;

    @include below(lg) {
      max-height: 45px;
    }
  }

  &__icon {
    font-size: 60px;
    transition: all $transition;
  }
}



