// Display
@each $bp in map-keys($breakpoints) {
  @if $bp != xs {
    .hidden-#{$bp}-up {
      @include above($bp) {
        display: none !important;
      }
    }
    .hidden-#{$bp}-down {
      @include below($bp) {
        display: none !important;
      }
    }
  }
}

.visually-hidden {
  @include visually-hidden();
}


// Spacing
@each $prop, $abbrev in (margin: m, padding: p) {
  @each $size, $value in $spacers {
    .#{$abbrev}-#{$size} { #{$prop}:         $value !important; }
    .#{$abbrev}t-#{$size} { #{$prop}-top:    $value !important; }
    .#{$abbrev}r-#{$size} { #{$prop}-right:  $value !important; }
    .#{$abbrev}b-#{$size} { #{$prop}-bottom: $value !important; }
    .#{$abbrev}l-#{$size} { #{$prop}-left:   $value !important; }

    // Axes
    .#{$abbrev}x-#{$size} {
      #{$prop}-right:  $value !important;
      #{$prop}-left:   $value !important;
    }
    .#{$abbrev}y-#{$size} {
      #{$prop}-top:    $value !important;
      #{$prop}-bottom: $value !important;
    }
  }
}


// Grid
.row {
  @include row();
}

%col-ready {
  @include col-ready();
}

@each $bp in map-keys($breakpoints) {
  @for $i from 1 through $grid-columns {
    .col-#{$bp}-#{$i} {
      @extend %col-ready;
    }
  }

  @include above($bp) {
    @for $i from 1 through $grid-columns {
      .col-#{$bp}-#{$i} {
        @include col($i / $grid-columns);
      }
    }

    @each $modifier in (pull, push) {
      @for $i from 0 through $grid-columns {
        .#{$modifier}-#{$bp}-#{$i} {
          @include col-modifier($modifier, $i / $grid-columns)
        }
      }
    }

    @for $i from 0 through ($grid-columns - 1) {
      @if $bp != xs or $i != 0 {
        .offset-#{$bp}-#{$i} {
          @include col-modifier(offset, $i / $grid-columns)
        }
      }
    }
  }
}





