// Utilities
@mixin font-face($name, $path, $weight: null, $style: null, $exts: eot woff2 woff ttf svg) {
  $src: null;

  $extmods: (
    eot: "?",
    svg: "#" + str-replace($name, " ", "_")
  );

  $formats: (
    otf: "opentype",
    ttf: "truetype"
  );

  @each $ext in $exts {
    $extmod: if(map-has-key($extmods, $ext), $ext + map-get($extmods, $ext), $ext);
    $format: if(map-has-key($formats, $ext), map-get($formats, $ext), $ext);
    $src: append($src, url(quote($path + "." + $extmod)) format(quote($format)), comma);
  }

  @font-face {
    font-family: quote($name);
    font-style: $style;
    font-weight: $weight;
    src: $src;
  }
}

@mixin placeholder() {
  &::-webkit-input-placeholder {
    @content;
  }
  &:-moz-placeholder {
    opacity: 1;
    @content;
  }
  &::-moz-placeholder {
    opacity: 1;
    @content;
  }
  &:-ms-input-placeholder {
    @content;
  }
}

@mixin hyphenation() {
  word-break: break-all;
  word-wrap: break-word;
  hyphens: auto;
}

@mixin text-truncate() {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

@mixin visually-hidden()  {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0,0,0,0);
  border: 0;
}

@mixin above($min) {
  @if type_of($min) == string {
    $min: map-get($breakpoints, $min);
  }

  @if $min != 0 {
    @media (min-width: $min) {
      @content;
    }
  }
  @else {
    @content;
  }
}

@mixin below($max) {
  @if type_of($max) == string {
    $max: if($max != xs, map-get($breakpoints, $max) - 1, 0);;
  }

  @if $max != 0 {
    @media (max-width: $max) {
      @content;
    }
  }
}

@mixin between($min, $max) {
  @include above($min) {
    @include below($max) {
      @content;
    }
  }
}

@mixin linear(
  $property,
  $value-start,
  $value-end,
  $width-start: $width-start,
  $width-end: $width-end,
  $template: "#"
) {
  @include below($width-start) {
    $value: $value-start;
    #{$property}: unquote(str-replace($template, "#", $value));
  }

  @include between($width-start, $width-end) {
    $value: linear($value-start, $value-end, $width-start, $width-end);
    #{$property}: unquote(str-replace($template, "#", $value));
  }

  @include above($width-end) {
    $value: $value-end;
    #{$property}: unquote(str-replace($template, "#", $value));
  }
}


// Components
@mixin button() {
  cursor: pointer;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  vertical-align: middle;
  text-align: center;

  @at-root a#{&}, a#{&}:hover {
    text-decoration: none;
  }
}

@mixin icon($name, $responsive: false, $map: $sprite-map, $retina: false) {
  $icon: map-get($map, $name);
  $image: map-get($icon, image);
  $sprite-width: map-get($icon, sprite-width);
  $sprite-height: map-get($icon, sprite-height);
  $width: map-get($icon, width);
  $height: map-get($icon, height);
  $x: map-get($icon, x);
  $y: map-get($icon, y);

  @if $responsive {
    $pos-x: 0;
    $pos-y: 0;

    @if ($sprite-width - $width) != 0 {
      $pos-x: ($x * -1) / ($sprite-width - $width);
    }

    @if ($sprite-height - $height) != 0 {
      $pos-y: ($y * -1) / ($sprite-height - $height);
    }

    padding-bottom: percentage($height / $width);

    $sprite-width: percentage($sprite-width / $width);
    $sprite-height: percentage($sprite-height / $height);
    $width: 100%;
    $height: 0;
    $x: percentage($pos-x);
    $y: percentage($pos-y);
  }
  @else {
    $sprite-width: rem($sprite-width);
    $sprite-height: rem($sprite-height);
    $width: rem($width);
    $height: rem($height);
    $x: rem($x);
    $y: rem($y);
  }

  background-image: $image;
  background-size: $sprite-width $sprite-height;
  background-position: $x $y;
  width: $width;
  height: $height;

  @if $retina {
    @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
      background-image: str-replace($image, ".png", "@2x.png");
    }
  }
}

@mixin triangle($size, $color, $direction) {
  $width: nth($size, 1);
  $height: nth($size, length($size));
  $foreground-color: nth($color, 1);
  $background-color: if(length($color) == 2, nth($color, 2), transparent);
  height: 0;
  width: 0;

  @if ($direction == up) or ($direction == down) or ($direction == right) or ($direction == left) {
    $width: $width / 2;
    $height: if(length($size) > 1, $height, $height/2);

    @if $direction == up {
      border-bottom: $height solid $foreground-color;
      border-left: $width solid $background-color;
      border-right: $width solid $background-color;
    } @else if $direction == right {
      border-bottom: $width solid $background-color;
      border-left: $height solid $foreground-color;
      border-top: $width solid $background-color;
    } @else if $direction == down {
      border-left: $width solid $background-color;
      border-right: $width solid $background-color;
      border-top: $height solid $foreground-color;
    } @else if $direction == left {
      border-bottom: $width solid $background-color;
      border-right: $height solid $foreground-color;
      border-top: $width solid $background-color;
    }
  } @else if ($direction == up-right) or ($direction == up-left) {
    border-top: $height solid $foreground-color;

    @if $direction == up-right {
      border-left:  $width solid $background-color;
    } @else if $direction == up-left {
      border-right: $width solid $background-color;
    }
  } @else if ($direction == down-right) or ($direction == down-left) {
    border-bottom: $height solid $foreground-color;

    @if $direction == down-right {
      border-left:  $width solid $background-color;
    } @else if $direction == down-left {
      border-right: $width solid $background-color;
    }
  } @else if ($direction == inset-up) {
    border-color: $background-color $background-color $foreground-color;
    border-style: solid;
    border-width: $height $width;
  } @else if ($direction == inset-down) {
    border-color: $foreground-color $background-color $background-color;
    border-style: solid;
    border-width: $height $width;
  } @else if ($direction == inset-right) {
    border-color: $background-color $background-color $background-color $foreground-color;
    border-style: solid;
    border-width: $width $height;
  } @else if ($direction == inset-left) {
    border-color: $background-color $foreground-color $background-color $background-color;
    border-style: solid;
    border-width: $width $height;
  }
}


// Layout
@mixin clearfix() {
  &:after {
    content: "";
    display: table;
    clear: both;
  }
}

@mixin container($max-width: $max-width) {
  min-width: $min-width;
  max-width: $max-width;
  padding-right: $site-padding;
  padding-left: $site-padding;
  margin-left: auto;
  margin-right: auto;
};

@mixin fit-to-viewport($side: both) {
  $offset: calc((100vw - (#{$max-width} - #{$site-padding * 2})) / -2);

  @if $side == left {
    margin-left: $site-padding * -1;

    @include above($max-width + 1) {
      margin-left: $offset;
    }
  }
  @else if ($side == right) {
    margin-right: $site-padding * -1;

    @include above($max-width + 1) {
      margin-right: $offset;
    }
  }
  @else if ($side == both) {
    margin-left: $site-padding * -1;
    margin-right: $site-padding * -1;

    @include above($max-width + 1) {
      margin-left: $offset;
      margin-right: $offset;
    }
  }
}


// Grid
@mixin row($gutter: $grid-gutter) {
  display: flex;
  flex-wrap: wrap;
  margin-right: ($gutter / -2);
  margin-left:  ($gutter / -2);
}

@mixin col-ready($gutter: $grid-gutter) {
  position: relative;
  min-height: 1px;
  width: 100%;
  padding-right: ($gutter / 2);
  padding-left: ($gutter / 2);
}

@mixin col($size) {
  flex: 0 0 percentage($size);
  max-width: percentage($size);
}

@mixin col-offset($size) {
  margin-left: percentage($size);
}

@mixin col-push($size) {
  left: if($size > 0, percentage($size), auto);
}

@mixin col-pull($size) {
  right: if($size > 0, percentage($size), auto);
}

@mixin col-modifier($type, $size) {
  @if $type == push {
    @include col-push($size);
  }
  @else if $type == pull {
    @include col-pull($size);
  }
  @else if $type == offset {
    @include col-offset($size);
  }
}





