@import "../../styles/functions";
@import "../../styles/variables";
@import "../../styles/mixins";

.nav {
  display: flex;
  align-items: center;

  $this: &;

  &_header {

    @include below(lg) {
      display: block;
    }

    & + & {

      @include below(lg) {
        margin-top: 20px;
      }
    }
  }

  &_page {

    @include below(lg) {
      flex-wrap: wrap;
    }
  }

  &__item {

    #{$this}_page & {
      margin-right: 15.5px;
      margin-bottom: 10px;
    }

    & + & {
      margin-left: 13px;

      #{$this}_page & {
        margin-left: 0;
      }

      #{$this}_header & {

        @include below(lg) {
          margin-left: 0;
          margin-top: 20px;
        }
      }
    }

    &_is-active {

      a {
        color: #000000;
      }
    }
  }
}



