@import "../../styles/functions";
@import "../../styles/variables";
@import "../../styles/mixins";

.field {
  border-bottom: 1px solid #e1e1e1;
  display: flex;

  &__legend {
    color: #828282;
    flex: none;
  }

  &__input {
    border: none;
    background: none;
    flex: auto;
    padding-left: 8px;
    height: 35px;
  }

  &__textarea {
    height: 35px;
    border: none;
    background: none;
    flex: auto;
    padding-left: 8px;
    resize: none;
  }
}



