@import "../../styles/functions";
@import "../../styles/variables";
@import "../../styles/mixins";

.feature-item {
  text-align: center;
  max-width: 280px;

  @include below(lg) {
    margin-left: auto;
    margin-right: auto;
  }

  &_mobile {
    display: none;

    @include below(lg) {
      display: block;
    }
  }

  &_video {
    margin-top: 30px;
  }

  &__icon {
    font-size: 65px;
    line-height: 1;
    margin-bottom: 15px;
  }

  &__desc {
    font-size: 20px;
  }

  &__count {
    font-size: 100px;
    margin-bottom: 22px;
    line-height: 1;
    font-family: $font-secondary;
  }
}



