@import "../../styles/functions";
@import "../../styles/variables";
@import "../../styles/mixins";

.header {

  $this: &;

  &__container {
    position: relative;
    padding-top: 19px;
    padding-bottom: 19px;

    @include below(lg) {
      padding-top: 0;
      padding-bottom: 0;
      height: $header-height-mobile;
      display: flex;
      align-items: center;
      justify-content: flex-end;
    }

    &:after {
      content: '';
      background-color: #e1e1e1;
      position: absolute;
      height: 1px;
      bottom: 0;
      left: $site-padding;
      right: $site-padding;

      @include below(lg) {
        left: $site-padding-mobile;
        right: $site-padding-mobile;
      }
    }
  }

  &__box {

    @include below(lg) {
      position: absolute;
      left: 0;
      width: 100%;
      background-color: #ffffff;
      z-index: 10;
      top: $header-height-mobile - 1px;
      display: flex;
      flex-direction: row-reverse;
      padding-left: $site-padding-mobile;
      padding-right: $site-padding-mobile;
      padding-top: 31px;
      padding-bottom: 40px;
      transition: all $transition;
      opacity: 0;
      visibility: hidden;
      border-bottom: 1px solid #e1e1e1;
    }

    #{$this}_active & {

      @include below(lg) {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__panel {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include below(lg) {
      width: 50%;
      align-items: flex-start;
      flex-direction: column;
      justify-content: flex-start;
    }

    &:last-child {

      @include below(lg) {
        padding-right: 20px;
      }
    }

    &_top {
      margin-bottom: 5px;
    }
  }

  &__text {

    @include below(lg) {
      display: none;
    }
  }

  &__changes {
    display: none;
    margin-top: 18px;
    margin-left: -4px;

    .changes {
      text-align: left;
      display: inline-flex;

      + .changes {
        margin-top: 18px;
      }
    }

    @include below(lg) {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
  }
}



